/**
 * Original marked.js library renderer with fixes:
 * - No formatting for output HTML string &mdash; all newlines between tags are removed to create clean output.
 * - Changed long string concatenations to ES5 template strings.
 * - Changed code style.
 *
 * @see {@link https://github.com/chjj/marked#renderer} Methods description.
 * @param options
 * @constructor
 */
function Renderer(options) {
  this.options = options || {};
}

Renderer.prototype.code = function (code, lang, escaped) {
  if (this.options.highlight) {
    var out = this.options.highlight(code, lang);

    if (out !== null && out !== code) {
      escaped = true;
      code = out;
    }
  }

  if (!lang) {
    return "<pre><code>".concat(escaped ? code : escape(code, true), "</code></pre>");
  }

  var cssClass = this.options.langPrefix + escape(lang, true);
  return "<pre><code class=\"".concat(cssClass, "\">").concat(escaped ? code : escape(code, true), "</code></pre>");
};

Renderer.prototype.blockquote = function (quote) {
  return "<blockquote>".concat(quote, "</blockquote>");
};

Renderer.prototype.html = function (html) {
  return html;
};

Renderer.prototype.heading = function (text, level, raw) {
  return "<h".concat(level, ">").concat(text, "</h").concat(level, ">");
};

Renderer.prototype.hr = function () {
  return this.options.xhtml ? '<hr/>' : '<hr>';
};

Renderer.prototype.list = function (body, ordered) {
  var type = ordered ? 'ol' : 'ul';
  return "<".concat(type, ">").concat(body, "</").concat(type, ">");
};

Renderer.prototype.listitem = function (text) {
  return "<li>".concat(text, "</li>");
};

Renderer.prototype.paragraph = function (text) {
  return "<p>".concat(text, "</p>");
};

Renderer.prototype.table = function (header, body) {
  return "<table><thead>".concat(header, "</thead><tbody>").concat(body, "</tbody></table>");
};

Renderer.prototype.tablerow = function (content) {
  return '<tr>' + content + '</tr>';
};

Renderer.prototype.tablecell = function (content, flags) {
  var type = flags.header ? 'th' : 'td';
  var tag = flags.align ? "<".concat(type, " align=\"").concat(flags.align, "\">") : "<".concat(type, ">");
  return tag + content + "</".concat(type, ">");
}; // span level renderer


Renderer.prototype.strong = function (text) {
  return "<strong>".concat(text, "</strong>");
};

Renderer.prototype.em = function (text) {
  return "<em>".concat(text, "</em>");
};

Renderer.prototype.codespan = function (text) {
  return "<code>".concat(text.trim(), "</code>");
};

Renderer.prototype.br = function () {
  return this.options.xhtml ? '<br/>' : '<br>';
};

Renderer.prototype.del = function (text) {
  return "<del>".concat(text, "</del>");
};

Renderer.prototype.link = function (href, title, text) {
  if (this.options.sanitize) {
    var prot;

    try {
      prot = decodeURIComponent(unescape(href)).replace(/[^\w:]/g, '').toLowerCase();
    } catch (e) {
      return '';
    }

    if (prot.indexOf('javascript:') === 0 || prot.indexOf('vbscript:') === 0) {
      // jshint ignore:line
      return '';
    }
  }

  var out = '<a href="' + href + '"';

  if (title) {
    out += ' title="' + title + '"';
  }

  out += '>' + text + '</a>';
  return out;
};

Renderer.prototype.image = function (href, title, text) {
  var out = '<img src="' + href + '" alt="' + text + '"';

  if (title) {
    out += ' title="' + title + '"';
  }

  out += this.options.xhtml ? '/>' : '>';
  return out;
};

Renderer.prototype.text = function (text) {
  return text;
};

export default Renderer;

function escape(html, encode) {
  return html.replace(!encode ? /&(?!#?\w+;)/g : /&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;').replace(/'/g, '&#39;');
}

function unescape(html) {
  // explicitly match decimal, hex, and named HTML entities
  return html.replace(/&(#(?:\d+)|(?:#x[0-9A-Fa-f]+)|(?:\w+));?/g, function (_, n) {
    n = n.toLowerCase();

    if (n === 'colon') {
      return ':';
    }

    if (n.charAt(0) === '#') {
      return n.charAt(1) === 'x' ? String.fromCharCode(parseInt(n.substring(2), 16)) : String.fromCharCode(+n.substring(1)); // jscs:ignore
    }

    return '';
  });
}