// Exports an array with custom converters used by to-markdown library.
export default [// Converting code blocks with class name matching output from marked library.
{
  filter: function filter(node) {
    var regexp = /lang-(.+)/;
    return node.nodeName === 'PRE' && node.firstChild && node.firstChild.nodeName === 'CODE' && regexp.test(node.firstChild.className);
  },
  replacement: function replacement(content, node) {
    var regexp = /lang-(.+)/;
    var lang = regexp.exec(node.firstChild.className)[1];
    return '\n\n``` ' + lang + '\n' + node.firstChild.textContent + '\n```\n\n';
  }
}, // Converting empty links.
{
  filter: function filter(node) {
    return node.nodeName === 'A' && !node.getAttribute('href');
  },
  replacement: function replacement(content, node) {
    var title = node.title ? "\"".concat(node.title, "\"") : '';
    return "[".concat(content, "](").concat(title, ")");
  }
}, // Headers - fixing newline at the beginning.
{
  filter: ['h1', 'h2', 'h3', 'h4', 'h5', 'h6'],
  replacement: function replacement(content, node) {
    var hLevel = node.nodeName.charAt(1);
    var hPrefix = '';

    for (var i = 0; i < hLevel; i++) {
      hPrefix += '#';
    }

    return hPrefix + ' ' + content + '\n';
  }
}, // Inline code - fixing backticks inside code blocks.
{
  filter: function filter(node) {
    var hasSiblings = node.previousSibling || node.nextSibling;
    var isCodeBlock = node.parentNode.nodeName === 'PRE' && !hasSiblings;
    return node.nodeName === 'CODE' && !isCodeBlock;
  },
  replacement: function replacement(content) {
    // If content starts or ends with backtick - use double backtick.
    if (content.indexOf('`') > -1) {
      return '`` ' + content + ' ``';
    }

    return '`' + content + '`';
  }
}];